import loading from "@/common/mixin/loading.js"

import uniNavBar from "@/components/uni-ui/uni-nav-bar/uni-nav-bar.vue"
import price from "@/components/common/price.vue"
import uniNumberBox from "@/components/uni-ui/uni-number-box/uni-number-box.vue"

import {mapState,mapGetters,mapActions,mapMutations} from "vuex"

import commonList from "@/components/common/common-list.vue"
import skuPopup from '@/components/cart/sku-popup.vue';

export default {
	mixins:[loading],
	components: {
		uniNavBar,
		price,
		uniNumberBox,
		commonList,
		skuPopup
	},
	data() {
		return {
			isedit: false,
			hotList:[]
		}
	},
	computed: {
		...mapState({
			list:state=>state.cart.list,
			selectedList:state=>state.cart.selectedList
		}),
		...mapGetters([
			'checkedAll',
			'totalPrice',
			'disableSelectAll'
		])
	},
	onLoad() {
		this.getData()
	},
	beforeDestroy() {
		uni.$off('updateCart')
	},
	onPullDownRefresh() {
		this.getData()
	},
	methods:{
		...mapActions([
			'doSelectAll',
			'doDelGoods',
			'doShowPopup',
			'updateCartList'
		]),
		...mapMutations([
			'selectItem',
			'initCartList',
			'unSelectAll'
		]),
		changeNum(e,item,index){
			if (item.num === e) return;
			uni.showLoading({
				title:"加载中..."
			})
			this.$H.post('/cart/updatenumber/'+item.id,{
				num:e
			},{
				token:true
			}).then(res=>{
				console.log(res);
				item.num = e
				uni.hideLoading()
			})
		},
		// 订单结算
		orderConfirm(){
			if(this.selectedList.length === 0){
				return uni.showToast({
					title: '请选择要结算的商品',
					icon: 'none'
				});
			}
			uni.navigateTo({
				url: '../order-confirm/order-confirm?detail='+JSON.stringify(this.selectedList)
			});
		},
		showPopup(index,item){
			if(!this.isedit){
				return;
			}
			this.$H.get('/cart/'+item.id+'/sku',{},{
				token:true
			}).then(res=>{
				
				// 商品规格（选项部分）
				let check = item.skusText.split(',')
				res.selects = res.goods_skus_card.map((v,i)=>{
				  let selected = 0
				  let list = v.goods_skus_card_value.map((v1,i1)=>{
					  if(check[i] === v1.value){
						  selected = i1
					  }
					  return {
						  id:v1.id,
						  name:v1.value
					  }
				  })
				  return {
					id:v.id,
					title:v.name,
					selected,
					list:list
				  }
				})
				// 商品规格（匹配价格）
				res.goods_skus.forEach(item=>{
				  let arr = []
				  for (let key in item.skus) {
					arr.push(item.skus[key].value)
				  }
				  item.skusText = arr.join(',')
				})
				
				this.doShowPopup({
					index:index,
					data:res
				})
			})
		},
		// 获取数据
		getData(){
			// 获取购物车数据
			this.updateCartList().then(res=>{
				uni.stopPullDownRefresh()
			}).catch(err=>{
				uni.stopPullDownRefresh()
			})
			// 获取热门推荐
			this.$H.get('/goods/hotlist').then(res=>{
				this.hotList = res.map(v=>{
					return {
					  id:v.id,
					  cover:v.cover,
					  title:v.title,
					  desc:v.desc,
					  oprice:v.min_oprice,
					  pprice:v.min_price
					}
				})
			})
		}
	}
}
